.contact-us {
  padding: 47px 0 110px;

  @media screen and (max-width: 560px) {
    padding: 15px 0 70px;
  }

  &__form {
    max-width: 630px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px auto 0;

    @media screen and (max-width: 560px) {
      margin: 40px auto 0;
    }
  }
}

.line-1 {
  animation: move-lines 10s infinite;
}


@keyframes move-lines {
  0%, 100% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(30px, 66px);
  }
}

.line-2 {
  animation: move-lines-2 10s infinite;
}


@keyframes move-lines-2 {
  0%, 100% {
    transform: translate(30px, 66px);
  }
  50% {
    transform: translate(0, 0);
  }
}

.promo .line__top {
  width: 891px;
  height: 1404px;

  .line-1 {
    top: 0;
    right: 0;
  }

  .line-2 {
    top: 0;
    right: 0;
    transform: translateX(30px);
  }
}

.svg__lines.home-contact {
  opacity: 1;
  visibility: visible;
  z-index: -1;
  .line__top {
    top: calc(2870px - -80 * (100vw - 1280px) / (2560 - 1280));
    right: calc(-690px - 120 * (100vw - 1280px) / (2560 - 1280));
    width: calc(999px + 333 * (100vw - 1280px) / (2560 - 1280));
    height: calc(1459px + 485 * (100vw - 1280px) / (2560 - 1280));
    @media screen and (max-width: 1130px) {
      top: calc(2780px - -280 * (100vw - 320px) / (1130 - 320));
      right: calc(-650px - 20 * (100vw - 320px) / (1130 - 320));
      width: calc(782px + 212 * (100vw - 320px) / (1130 - 320));
      height: calc(1142px + 317 * (100vw - 320px) / (1130 - 320));
    }
  }
}

.svg__lines_right.home-prices {
  opacity: 1;
  visibility: visible;

  .line__top {
    top: calc(1510px - -320 * (100vw - 1280px) / (2560 - 1280));
    left: calc(-1200px - 200 * (100vw - 1280px) / (2560 - 1280));
    width: calc(1491px + 333 * (100vw - 1280px) / (2560 - 1280));
    height: calc(1994px + 485 * (100vw - 1280px) / (2560 - 1280));
    transform: rotate(-152deg);
    @media screen and (max-width: 1130px) {
      top: calc(2063px - 150 * (100vw - 560px) / (1130 - 560));
      left: calc(-650px - 340 * (100vw - 560px) / (1130 - 560));
      width: calc(782px + 392 * (100vw - 560px) / (1130 - 560));
      height: calc(1142px + 457 * (100vw - 560px) / (1130 - 560));
    }
    @media screen and (max-width: 560px) {
      left: calc(-690px - 280 * (100vw - 320px) / (560 - 320));
    }
  }
}

.svg__shapes.home-contact {
  transform: scaleX(-1);
  .svg {
    &.svg_top {
      opacity: 1;
      top: calc(3240px - 430 * (100vw - 1440px) / (2560 - 1440));
      left: calc(1090px - -960 * (100vw - 1440px) / (2560 - 1440));
      width: calc(900px + 600 * (100vw - 1440px) / (2560 - 1440));
      height: calc(900px + 600 * (100vw - 1440px) / (2560 - 1440));
      right: unset;
      @media screen and (max-width: 1330px) {
