      left: calc(1140px - -1000 * (100vw - 1440px) / (2560 - 1440));
      width: calc(618px + 426 * (100vw - 1440px) / (2560 - 1440));
      height: calc(1127px + 575 * (100vw - 1440px) / (2560 - 1440));
      @media screen and (max-width: 1330px) {
        display: none;
      }
    }
